.ly-header{
  background: $bg-light-gray;
  animation-duration: 3s;
  animation-name: headerFadeIn;
}
@keyframes headerFadeIn {
  from {
    background: $bg-white;
  }
  50% {
  }
  to {
    background: #eff1f3;
  }
}

.svg-logoMark{
  position: relative;
  fill: rgb(101, 206, 233);
  width: 75%;
  opacity: 0.3;
  // filter: drop-shadow(0 0px 3px rgba(0, 0, 0, .3));
  // margin-top: -180px;
  // top: 0;
  animation-duration: 1s;
  animation-name: slideIn;
  left: -100px;  
}

@keyframes slideIn {
  from {
    left: -200px;
    opacity: 0.1;
  }
  50% {
  }
  to {
    left: -100px;
    opacity: 0.3;
  }
}


