.bl-recentCnt{
  position: relative;
  padding-bottom: 16px;
  margin-top: 64px;
  overflow: hidden;
  border-bottom: 1px dashed $border-gray;
  .svg-note{
    width: 20px;
    margin: 0 auto 4px;
    display: block;
  }
  &_ttl{
    margin: 0 0 16px;
    font-size: $font-size-16;
    text-align: center;
    // color: $primary-turquoise;
    letter-spacing: 0.2em;
  }
  &_pages{
    list-style: none;
    margin: 0 0 32px;
    padding: 0;
  }
  &_list{
    width: 100%;
    padding: 22px 12px 8px;
    background: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    margin-bottom: 12px;
    border-radius: 4px;
    // &:last-of-type{
    //   border: none;
    // }
  }
  &_link{
    margin-bottom: 8px;
    padding:0 12px 12px 0;
    display: flex;
    align-items: center;
    position: relative;
    border-bottom: 1px dotted $border-gray;
    &:after{
      content: "";
      width: 8px;
      height: 8px;
      transform: rotate(45deg);
      border-top: 2px solid $primary-turquoise;
      border-right: 2px solid $primary-turquoise;
      position: absolute;
      right: 0;
      top: 16px;
    }
  }
  &_linkImg{
    width: 36px;
    min-width: 36px;
    height: 36px;
    border-radius: 4px;
    background: #1b1b1b;
    display: flex;
    justify-content: center;
    margin-right: 12px;
    align-items: center;
  }
  &_linkImgtxt{
    margin: 0;
    font-size: 0;
    transform: rotate(24deg);
    &:first-letter{
      color: #fff;
      font-size: 64px;
      font-weight: bold;
      line-height: 1;
    }
  }
  &_linkTxt{
    color: $font-black;
    font-size: $font-size-13;
    font-weight: bold;
    line-height: 1.4;
    margin: 0;
  }
  // &_pagettl{
  //   font-size: $font-size-14;
  //   margin: 0 0 8px;
  //   line-height: 1.3;
  // }
  &_detail{
    display: flex;
    justify-content: space-between;
  }
  .tagList{
    display: flex;
    list-style: none;
    padding: 0;
    margin-bottom: 2px;
  }
  .tag{
    position: relative;
    padding: 4px 10px;
    border-radius: 40px;
    background: #fff;
    border: 1px solid $primary-turquoise;
    color: $primary-turquoise;
    font-size: $font-size-12;
    line-height: 1;
    & + .tag{
      margin-left: 4px;
    }
    &.active-tag{
      color: #fff;
      background-color: $primary-turquoise;
    }
  }
  .relatedList{
    position: fixed;
    list-style: none;
    width: 72%;
    min-height: 420px;
    top: 100px;
    right: -100%;
    z-index: 21;
    background: rgba(255, 255, 255, 0.86);
    padding: 20px 8px 12px 12px;
    // border-radius: 6px;
    box-shadow: $box-shadow;
    display: block;
    transition: $transition;
    &.active-list{
      right: 0;
      // display: block;
    }
  }
  &_relatedTtl{
    display: flex;
    justify-content: space-between;
    margin:0 8px 12px 0;
    h3{
      margin: 0;
    }
  }
  &_relatedClose{
    font-size: $font-size-22;

    display: block;
  }
  &_relatedMenuLists{
    list-style: none;
    padding-left: 0;
  }
  &_relatedLink{
    color: $font-black;
    font-size: $font-size-12;
    line-height: 1.4;
    display: block;
    padding: 12px 0;
  }
  .bl-recentCnt_relatedMenuList + .bl-recentCnt_relatedMenuList{
    border-top: 1px dashed $border-gray;
  }
  // &_relatedList{
  //   line-height: 1.3;
  // }
  // &_relatedLink{
  //   font-size: $font-size-13;
  // }

  &_updateDate{
    font-size: $font-size-11;
    color: $font-gray;
    margin: 0;
  }
}

.bl-pageNav{
  padding-top: 64px;
  padding-bottom: 64px;
  &_list{
    list-style: none;
    padding: 0;
    margin: 0;
    background:$bg-white;
  }
  &_cnt{
    // background: #fff;
    width: 100%;
    padding: $space-unit * 1 $space-unit * 2;
    border-bottom: 1px solid $border-gray;
    position: relative;
    &:after{
      content: "";
      position: absolute;
      display: inline-block;
      width: 8px;
      height: 8px;
      right: $space-unit * 2;
      bottom: 20px;
      border-bottom: 1px solid $font-gray;
      border-right: 1px solid $font-gray;
      transform: rotate(45deg);
      transition: $transition;
    }
    &.is-active:after{
      transform: rotate(-135deg);
    }
    &.not-focus{
      h3{
        color: $font-light-gray;
      }
      .bl-pageNav_cntNumber{
        color: $font-light-gray;
      }
    }
  }
  &_index{
    display: block; 
    color: $font-black;
    // &:hover{
    //   border-color:$gray-100;
    //   color: $font-gray;
    // }
  }
  &_ttl{
      display: flex;
      align-items: baseline;
    h3{
      font-size: $font-size-15;
      font-weight: normal;
      padding-right: 12px;
      margin: 0;
      color: $font-black;
      transition: $transition;
    }
  }
  &_cntNumber{
    font-size: $font-size-10;
    align-items: center;
    background: #eee;
    width: 18px;
    text-align: center;
    height: 18px;
    line-height: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    padding: 1px;
    color: $font-dark-gray;
    transition: $transition;
  }
  &_txt{
    font-size: $font-size-15;
    margin-bottom: 0;
  }
  &_menu{
    line-height: 0;
    height: 0;
    overflow: hidden;
    opacity: 0.3;
    transition: $transition;
    padding: 0 0 0 5%;
    // background: $bg-light-gray;
    &.is-open{
      line-height: normal; /* numberに書き換える*/
      height: auto;
      opacity: 1;
      padding: 4px 0 4px 5%;
      ul{
        li{
          padding: 8px;
          border-bottom:1px solid $border-gray;
          &:last-of-type{
            border-bottom: none;
          }
        }
      }
    }
  }
  &_menuList{
    list-style: none;
    padding-left: 0;
  }
  &_link{
    color: $font-black;
    font-size: $font-size-14;
  }
}

.el-closePopup{
  display: block;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 1;
	height: 100vh;
	width: 100%;
	visibility: hidden;
	// opacity: 0;
	pointer-events: none;
	transition: $transition;
	&.is-active{
    // background-color: rgba(0, 0, 0, 0.295);
		pointer-events: auto;
		visibility: visible;

	}
}
.homeProfile{
  padding: 42px $gutter-width 64px;
  z-index: 2;
  &_ttl{
    margin: 0 0 12px;
    font-size: $font-size-14;
    text-align: center;
  }
  .svg-characotorIcon{
    width: 72px;
    margin: 0 auto 12px;
    display: block;
  }
  &_txt{
    font-size: $font-size-13;
  }
  .homeCareer{
    margin-bottom: 32px;
    ul{
      display: flex;
      overflow: scroll;
      list-style: none;
      margin: 0 -5%;
      padding-left: $gutter-width;
      li{
        min-width: 120px;
        border: 1px solid $border-gray;
        padding: 6px;
        border-radius: 4px;
        font-size: $font-size-13;
        line-height: 1.4;
        + li{
          margin-left: 12px;
        }
      }
    }
  }
  .homeSkill{
    h3{
      padding-bottom: 4px;
      margin-bottom: 16px;
      border-bottom: 1px dashed $border-gray;
      font-size: $font-size-15;
    }
    h4{
      font-size: $font-size-13;
      margin-bottom: 8px;
      border-left: 2px solid $border-gray;
      padding-left: 6px;
    }
    ul{
      list-style: none;
      display: flex;
      flex-wrap: wrap;
      padding: 0 0 0 6px;
      margin: 0;
      li{
        z-index: 10;
        h5{
          padding: 0 8px;
          // border: 1px solid $border-gray;
          font-size: $font-size-12;
          border-radius: 4px;
          background: #fff;
          border: 1px solid $border-gray;
          margin:0 6px 6px 0;
        }
        p{
          display: none;
          font-size: $font-size-12;
          padding: 4px 8px;
          border-radius: 4px;
          background: #ccc;
          position: absolute;
          margin: 0 5%;
          left: 0;
        }
        &:last-of-type{
          margin-right: 0;
        }
        // &:hover{
          //   p{
            //     display: block;
            //   }
            // }
      }
    }
    .popupTxt{
      font-size: $font-size-12;
      padding: 8px;
      border-radius: 4px;
      background: #fff;
      box-shadow: 2px 2px 10px #ddd;

      // position: absolute;
      // margin: 0 5%;
      left: 0;
    }
  } 
}



